import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService } from '../services/auth.service';
import { Title, Meta } from '@angular/platform-browser';
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  registerForm : any;
  nameError : any;
  fieldTextType=false;
  fieldTextType1=false;
  passwordError : any;
  confirmPasswordError : any;
  registrationError : any;
  availableUsers : any[] =[];
  submitted:any;
  constructor(
    private http : HttpClient,
    private authService: AuthService,
    private router: Router,
    private titleService: Title,
    private metaTagService: Meta ) {
      this.authService.getAllUsers().subscribe((data:any[])=>{
        this.availableUsers = data;
        // console.log("Available users",this.availableUsers);
      })
    }

  ngOnInit(): void {

    this.titleService.setTitle("Register");
    this.metaTagService.addTags([
      {name:"description", content:"Sign Up here to try out the best out of Bloggle"},
      {name:"author", content: "Piyush Chouhan M, Nishant Bhushan, Kedar R, Mohanavel R, Kumaran R"},
      {name:"robots", content:"index,follow"},
      {name:"keywords",content:"Register, Sign up, sign"},
      {httpEquiv:'Content-Type', content:"text/html", charset:"UTF-8"}                       
      ]);

    this.registerForm = new FormGroup({
      firstname : new FormControl(null,[Validators.required,Validators.minLength(2),Validators.maxLength(20)]),
      lastname : new FormControl(null,[Validators.required,Validators.minLength(1),Validators.maxLength(20)]),
      dob : new FormControl(null,Validators.required),
      email : new FormControl(null,[Validators.required,Validators.email]),
      username : new FormControl(null,[Validators.required,Validators.minLength(2),Validators.maxLength(15)]),
      password : new FormControl(null,[Validators.required,Validators.minLength(2)]),
      confirmPassword : new FormControl(null,[Validators.required])
    })
  }

  get f() { return this.registerForm.controls; }
  checkUserName()
  {
    this.nameError = false;
    // console.log(this.registerForm.value.username);
    this.availableUsers.forEach(user =>{
      if (user == this.registerForm.value.username)
      {
        this.nameError = true;
        return;
      }
    })
  }

  checkPassword()
  {
    this.confirmPasswordError = false;
    if(this.registerForm.value.password !== this.registerForm.value.confirmPassword)
    {
      this.confirmPasswordError = true;
    }
  }

  async onSubmit(): Promise<any> {


    this.submitted = true;
    if (this.registerForm.invalid) {
      return;
  }
    if (this.registerForm.dirty && this.registerForm.valid) {
      if (this.registerForm.value.email === null || this.registerForm.value.password === null) {
        this.registerForm.reset();
      }
      else {
        this.authService.register(this.registerForm.value)
          .subscribe((data) => {
            if(data!=null)
            {
              this.authService.login(this.registerForm.value)
              .subscribe((response)=>{
                // console.log(response);
                localStorage.setItem('User', JSON.stringify(response));
                this.router.navigate(['/home']);
                this.authService.isLoggedIn.next(true);
                this.authService.userAuthenticated = true;
                this.registerForm.reset();
              })
            }
          }, (err) => {
            this.registerForm.reset();
            this.authService.isLoggedIn.next(false);
            this.authService.userAuthenticated = false;
            if (err.status === 400) {
              this.registrationError = "Invalid Form Data.";
              // console.log(this.registrationError);
            } else {
              this.registrationError = "Technical Error occurred.";
              // console.log(this.registrationError);  
            }
          });
      }
    } 

  }
  toggleFieldTextType() {
    this.fieldTextType = !this.fieldTextType;
  }
  toggleFieldTextType1() {
    this.fieldTextType1 = !this.fieldTextType1;
  }

}
