import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { AuthService } from './auth.service';
import { DomSanitizer } from '@angular/platform-browser';
@Injectable({
  providedIn: 'root'
})

export class DashboardService {
  api = environment.SERVER_URL;
  constructor(private httpclient:HttpClient,private _authservices:AuthService,private _sanitizer:DomSanitizer) { }
  getDashboard():Observable<any>{
    return this.httpclient.get<any>(`${this.api}/api/Blog/blogsOfUser?username=${this._authservices.isAuthenticated().userName}`);
  }
  getBookMark():Observable<any>{
    return this.httpclient.get<any>(`${this.api}/api/bookmark`);
  }
  getComment():Observable<any>{
    return this.httpclient.get<any>(`${this.api}/api/comments?username=1`);
  }
  getImage(imageUrl: string): Promise<Blob> {
    return this.httpclient.get(this.api+'/api/media/'+imageUrl, { responseType: 'blob' }).toPromise();
  }
  deleteComment(id){
    return this.httpclient.delete(this.api+"/api/comments/"+id);
  }
  deleteBookmark(id){
    return this.httpclient.delete(this.api+"/api/bookmark/"+id);
  }
  deleteBlog(id){
    return this.httpclient.delete(this.api+"/api/Blog/"+id);
  }

  async ImageAdder(blog)
  {
      if(blog.MediaId != null)
      {
        let baseImage = await this.getImage(blog.MediaId);
        let objectURL = URL.createObjectURL(baseImage);
        blog['Media'] = this._sanitizer.bypassSecurityTrustUrl(objectURL);     
      }
  }
}
