import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders, HttpParams} from '@angular/common/http';
import {Observable} from 'rxjs';
import {environment} from 'src/environments/environment';
@Injectable({
  providedIn: 'root'
})
export class ProfileService {
  api = environment.SERVER_URL;
  constructor(private http:HttpClient) { }

  updateUser(userData:any) : Observable<any> {

    
    
    return this.http.put(`${this.api}/api/Account/UpdateUser`, 
    {
      "Email" : userData.Email,
      "About" :userData.About,
      "Facebook" : userData.Facebook,
      "Twitter" : userData.Twitter,
      "PhoneNumber" :userData.PhoneNumber,
      "Instagram": userData.Instagram,
      "ProfilePicture" : userData.ProfilePicture
    });

    
  }

    updatePassword(passData:any) : Observable<any> {
      return this.http.post(`${this.api}/api/Account/ChangePassword`,{
        "OldPassword" : passData.OldPassword,
        "NewPassword" : passData.NewPassword,
        "ConfirmPassword" : passData.ConfirmPassword
            
      });
    
  }
   getUserInfo(userName):Observable<any>{
     return this.http.get<any>(`${this.api}/api/Account/UserInfo?userName=${userName}`);
   }
   getImage(imageUrl: any): Observable<Blob> {
    return this.http.get(this.api+'/api/media/'+imageUrl, { responseType: 'blob' });
  }
  
  updateImage(form: any): Observable<any> {  
    return this.http.post(`${this.api}/api/media`,form);
  }

}
