import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {environment} from 'src/environments/environment';
@Injectable({
  providedIn: 'root'
})
export class FaqServiceService {
  api = environment.SERVER_URL;
  constructor(private http:HttpClient) { }
  getFAQList():Observable<any[]>{
    return  this.http.get<any>(`${this.api}/api/faq`);

  }

  votingMeter(id,votetype) : Observable<any>
  {
    return this.http.get(`${this.api}/api/FAQ/vote?id=${id}&votetype=${votetype}`);
  }
}
