import { AfterViewInit, Component, OnInit } from '@angular/core';
import { BlogService } from '../services/blog.service';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { Title, Meta } from '@angular/platform-browser';
import { DarkModeConfig } from '../utility/darkmode/modeConfig';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit,AfterViewInit {
  latestBlogs1 : any;
  latestBlogs4 : any[] = [];
  trendingBlogs :any[] = [];
  popularBlogs : any[] = [];
  technologyBlogs: any[] = [];
  categories : any[] = [];
  categoryXBlogs : any[] =[];
  selection : any = 1;
  darkMode: any;
  constructor(public blogService : BlogService, private titleService: Title,private metaTagService: Meta) {
    this.blogService.getCategoryXBlogs(1).subscribe((data:any[])=>{
      data.forEach(blog =>{
        this.blogService.ImageAdder(blog).then();
      })
      this.technologyBlogs = data;
      
    })
    this.blogService.getCategoryXBlogs(this.selection).subscribe((data:any[])=>{
      data.forEach(blog =>{
        this.blogService.ImageAdder(blog).then();
      })
      this.categoryXBlogs = data;

      // console.log(this.categoryXBlogs);
    })
  }

ngOnInit(): void {


    //Meta tags for SEO
    this.titleService.setTitle("Bloggle - The Personalised Blog");
    this.metaTagService.addTags([
      {name:"description", content:"Bloggle is a creation by innovation. This is a blogging site that has many of the features that you can think of."},
      {name:"author", content: "Piyush Chouhan M, Nishant Bhushan, Kedar R, Mohanavel R, Kumaran R"},
      {name:"robots", content:"index,follow"},
      {name:"keywords",content:"Blogs, Blogging website, Bloggle, Personalised blogs, blog site"},
      {httpEquiv:'Content-Type', content:"text/html", charset:"UTF-8"}                       
      ]);

    //Fetching Latest blogs
    this.blogService.getFeaturingBlogs("latest",5).subscribe((data:any[])=>{
      data.forEach(blog =>{
        this.blogService.ImageAdder(blog).then();
      })
      this.latestBlogs1 = data[0];
      this.latestBlogs4 = data.slice(1,5);
    })

    //Fetching trending blogs
    this.blogService.getFeaturingBlogs("trending",12).subscribe((data:any[])=>{
      data.forEach(blog =>{
        this.blogService.ImageAdder(blog).then();
      })
      this.trendingBlogs = data;
    })

    this.blogService.getFeaturingBlogs("popular",12).subscribe((data:any[])=>{
      data.forEach(blog =>{
        this.blogService.ImageAdder(blog).then();
      })
      this.popularBlogs = data;
    })

    this.blogService.getAllCategories().subscribe((data:any[])=>{
      this.categories = data;
    })

    //Dark Mode 
    this.darkMode = new DarkModeConfig();
  }

  ngAfterViewInit(): void {
    var mode = this.darkMode.getCookie('darkmode');
    if(mode === "true")
    {
      // (<HTMLInputElement>document.getElementById('mode')).checked = true;
      this.darkMode.darkModeToggler(true);
    }
    else
    {
      // (<HTMLInputElement>document.getElementById('mode')).checked = false;
      this.darkMode.darkModeToggler(false);
    }
  }
  

  getSelectedCategory(selectvalue)
  {
    this.selection = selectvalue;
    // console.log(this.selection);
  }

  changeSelection(category)
  {
    this.selection = category;
    this.blogService.getCategoryXBlogs(this.selection).subscribe((data:any[])=>{
      data.forEach(blog =>{
        this.blogService.ImageAdder(blog).then();
      })
      this.categoryXBlogs = data;
      // console.log(this.categoryXBlogs);
    })
    // console.log(this.selection);
  }

  customOptions: OwlOptions = {
    loop: false,
    rewind:true,
    mouseDrag: false,
    touchDrag: true,
    pullDrag: false,
    dots: false,
    navSpeed: 700,
    navText: ["<i class='fa fa-angle-left'></i>", "<i class='fa fa-angle-right'></i>"],
    responsive: {
      0: {
        items: 1
      },
      600: {
        items: 2
      },
      1000: {
        items: 4
      }
    },
    nav: true
  }

  customOptions2: OwlOptions = {
    loop: false,
    rewind:true,
    mouseDrag: false,
    touchDrag: false,
    pullDrag: false,
    dots: false,
    navSpeed: 700,
    navText: ["<i class='fa fa-angle-left'></i>", "<i class='fa fa-angle-right'></i>"],
    items:1,
    nav: true
  }


}

  


