import {Component, Inject} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {ReportService} from '../../services/report.service';
import Swal from 'sweetalert2'
@Component({
  selector: 'app-report-content',
  templateUrl: './report-content.component.html',
  styleUrls: ['./report-content.component.css']
})
export class ReportContentComponent {
  selectedValue:any;
  reportTypes : string[] = ["Sexual Content", "Violent or repulsive content", "Hateful or abusive content", "Spam or misleading"];
  constructor(
    public dialogRef: MatDialogRef<ReportContentComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private service: ReportService) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

  reportContent()
  {
    var report = {};
    report["ContentType"] = this.data.ContentType;
    report["ContentId"] = this.data.ContentId;
    report["ReportType"] = this.selectedValue;
    this.service.createReport(report).subscribe((response)=>{
      Swal.fire({
        title: 'Done!',
        text: 'Successfully reported',
        icon: 'success',
        showConfirmButton: false,
        timer: 1500
      })
      this.dialogRef.close();
    },(err:any)=>{
      Swal.fire({
        title: 'Error!',
        text: 'Problem in reporting',
        icon: 'error',
        showConfirmButton: false,
        timer: 1500
      })
      this.dialogRef.close();
    })

    // console.log(this.selectedValue);
  }

}
