export class  DarkModeConfig
{
    darkModeToggler(mode:boolean)
    {
        if(mode)
        {
            var body = document.body;
            body.classList.add("dark-mode");
            
            var totalTitles = document.getElementsByClassName('fh5co_magna');
            for (let index = 0; index < totalTitles.length; index++) {
                (<HTMLElement>totalTitles[index]).style.color = "#fff";
            }
            var totalHeadings = document.getElementsByClassName('fh5co_heading');
            for (let index = 0; index < totalHeadings.length; index++) {
                (<HTMLElement>totalHeadings[index]).style.color = "#fff";
            }
            var totalTimings = document.getElementsByClassName('fh5co_mini_time');
            for (let index = 0; index < totalTimings.length; index++) {
                (<HTMLElement>totalTimings[index]).style.color = "#fff";
            }
            var totalSubTitles = document.getElementsByClassName('fh5co_consectetur');
            for (let index = 0; index < totalSubTitles.length; index++) {
                (<HTMLElement>totalSubTitles[index]).style.color = "alicblue";
            } 
            // var totalFA = document.getElementsByClassName('fa-lg');
            // for (let index = 0; index < totalFA.length; index++) {
            //     (<HTMLElement>totalFA[index]).style.color = "white";
            // }
            var totalFAQBtnLink = document.getElementsByClassName('btn-link');
            for (let index = 0; index < totalFAQBtnLink.length; index++) {
                (<HTMLElement>totalFAQBtnLink[index]).style.color = "white";
            } 
            var commentsTitle = document.getElementsByClassName('comments-title');
            for (let index = 0; index < commentsTitle.length; index++) {
                (<HTMLElement>commentsTitle[index]).style.color = "white";
            }
            var commentsName = document.getElementsByClassName('comment-author');
            for (let index = 0; index < commentsName.length; index++) {
                (<HTMLElement>commentsName[index]).style.color = "white";
            } 
            var codeSnippets = document.getElementsByClassName('ql-syntax');
            for (let index = 0; index < codeSnippets.length; index++) {
                (<HTMLElement>codeSnippets[index]).style.color = "white";
                (<HTMLElement>codeSnippets[index]).style.backgroundColor = "#300AAB";
            } 
            var rows = document.getElementsByClassName('tablerow');
            for (let index = 0; index < rows.length; index++) {
                (<HTMLElement>rows[index]).style.color = "#300AAB";
                (<HTMLElement>rows[index]).style.backgroundColor = "aliceblue";
            } 
            var footer = document.getElementsByClassName('fh5co_footer_bg');
            for (let index = 0; index < footer.length; index++) {
                (<HTMLElement>footer[index]).style.backgroundColor = "#02001b";
            }
            var viewsEye = document.getElementById('viewsCounter');
            if(viewsEye)
                (<HTMLElement>viewsEye).style.textShadow = "0 0 10px #fff, 0 0 20px #fff, 0 0 30px #e60073, 0 0 40px #e60073, 0 0 50px #e60073, 0 0 60px #e60073, 0 0 70px #e60073";

        }
        else
        {
            var body = document.body;
            body.classList.remove("dark-mode");
            
            var totalTitles = document.getElementsByClassName('fh5co_magna');
            for (let index = 0; index < totalTitles.length; index++) {
                (<HTMLElement>totalTitles[index]).style.color = "#000";
            }
            var totalHeadings = document.getElementsByClassName('fh5co_heading');
            for (let index = 0; index < totalHeadings.length; index++) {
                (<HTMLElement>totalHeadings[index]).style.color = "#222";
            }
            var totalTimings = document.getElementsByClassName('fh5co_mini_time');
            for (let index = 0; index < totalTimings.length; index++) {
                (<HTMLElement>totalTimings[index]).style.color = "#222";
            }
            var totalSubTitles = document.getElementsByClassName('fh5co_consectetur');
            for (let index = 0; index < totalSubTitles.length; index++) {
                (<HTMLElement>totalSubTitles[index]).style.color = "#777";
            }  
            // var totalFA = document.getElementsByClassName('fa-lg');
            // for (let index = 0; index < totalFA.length; index++) {
            //     (<HTMLElement>totalFA[index]).style.color = "black";
            // }  
            var totalFAQBtnLink = document.getElementsByClassName('btn-link');
            for (let index = 0; index < totalFAQBtnLink.length; index++) {
                (<HTMLElement>totalFAQBtnLink[index]).style.color = "black";
            }  
            var commentsTitle = document.getElementsByClassName('comments-title');
            for (let index = 0; index < commentsTitle.length; index++) {
                (<HTMLElement>commentsTitle[index]).style.color = "black";
            } 
            var commentsName = document.getElementsByClassName('comment-author');
            for (let index = 0; index < commentsName.length; index++) {
                (<HTMLElement>commentsName[index]).style.color = "#383b43";
            } 
            var codeSnippets = document.getElementsByClassName('ql-syntax');
            for (let index = 0; index < codeSnippets.length; index++) {
                (<HTMLElement>codeSnippets[index]).style.color = "#212529";
                (<HTMLElement>codeSnippets[index]).style.backgroundColor = "aliceblue";
            }
            var rows = document.getElementsByClassName('tablerow');
            for (let index = 0; index < rows.length; index++) {
                (<HTMLElement>rows[index]).style.color = "black";
                (<HTMLElement>rows[index]).style.backgroundColor = "aliceblue";
            } 
            var footer = document.getElementsByClassName('fh5co_footer_bg');
            for (let index = 0; index < footer.length; index++) {
                (<HTMLElement>footer[index]).style.backgroundColor = "#222";
            }
            var viewsEye = document.getElementById('viewsCounter');
            if(viewsEye)
            {
                (<HTMLElement>viewsEye).style.textShadow = "none";
            }

        }
    }

    navBarToggler(mode)
    {
        if(mode)
        {
            document.getElementsByTagName('nav')[0].classList.toggle('navbar-light');
            document.getElementsByTagName('nav')[0].classList.add('navbar-dark');
        }
        else
        {
            document.getElementsByTagName('nav')[0].classList.toggle('navbar-dark');
            document.getElementsByTagName('nav')[0].classList.add('navbar-light');
        }
    }

    getCookie(cname) {
        var name = cname + "=";
        var decodedCookie = decodeURIComponent(document.cookie);
        var ca = decodedCookie.split(';');
        for (var i = 0; i < ca.length; i++) {
            var c = ca[i];
            while (c.charAt(0) == ' ') {
                c = c.substring(1);
            }
            if (c.indexOf(name) == 0) {
                return c.substring(name.length, c.length);
            }
        }
        return "";
      }
    
}
