import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { CarouselModule } from 'ngx-owl-carousel-o';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { HomeComponent } from './home/home.component';
import { BlogsComponent } from './blogs/blogs.component';
import { AboutComponent } from './about/about.component';
import { FaqComponent } from './faq/faq.component';
import { ProfileComponent } from './profile/profile.component';
import { PublicProfileComponent } from './public-profile/public-profile.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { BookmarksComponent } from './bookmarks/bookmarks.component';
import { BlogComponent } from './blog/blog.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {MaterialModule} from './services/material.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import {AuthInterceptorService } from "./services/auth-interceptor.service";
import { CategoryXComponent } from './category-x/category-x.component';
import { CreateBlogComponent } from './create-blog/create-blog.component';
import {NgxPaginationModule} from 'ngx-pagination';

import { QuillModule } from 'ngx-quill';
import { AngularFireModule } from '@angular/fire';
import{AngularFireDatabaseModule} from '@angular/fire/database';
import {environment} from '../environments/environment';
import {AngularFirestoreModule} from '@angular/fire/firestore';
import {ContactusService} from './services/contactus.service';
import { AdminpageComponent } from './adminpage/adminpage.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import {MatTableModule} from '@angular/material/table';
import {AdminpageService} from './services/adminpage.service';
import { MatSortModule } from '@angular/material/sort';
import { ConfirmationComponent } from './utility/confirmation/confirmation.component';
import { AlertDialogComponent } from './utility/alert-dialog/alert-dialog.component';
import { ReportContentComponent } from './utility/report-content/report-content.component';
import  {  NgxEmojModule  }  from  'ngx-emoj';
@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    HomeComponent,
    BlogsComponent,
    AboutComponent,
    FaqComponent,
    ProfileComponent,
    PublicProfileComponent,
    DashboardComponent,
    LoginComponent,
    RegisterComponent,
    BookmarksComponent,
    BlogComponent,
    CategoryXComponent,
    CreateBlogComponent,
    AdminpageComponent,
    PageNotFoundComponent,
    ConfirmationComponent,
    AlertDialogComponent,
    ReportContentComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    FormsModule,
    HttpClientModule,
    ReactiveFormsModule,
    MaterialModule,
    CarouselModule,
    NgxPaginationModule,
    QuillModule.forRoot(),
    AngularFireModule.initializeApp(environment.firebase),
    AngularFireDatabaseModule,
    MatTableModule,
    MatSortModule,
    NgxEmojModule
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, 
      useClass: AuthInterceptorService, 
      multi: true 
    },
    ContactusService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
