import { Component, OnInit } from '@angular/core';
import {ProfileService} from '../services/profile.service';
import { DomSanitizer } from '@angular/platform-browser';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService } from '../services/auth.service';
import { Title, Meta } from '@angular/platform-browser';
@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  maxLimitError : boolean = false;
  currentUser: any;
  FormBuilder: any;
  constructor(private service: ProfileService,
    private sanitizer: DomSanitizer,
    private router:Router,
    private authService : AuthService,
    private titleService: Title,
    private metaTagService: Meta ) {
      this.currentUser = this.authService.isAuthenticated().userName;
     }
  pro:any = {};
  userdata:any;
  sub:any;
  responseUser:any;
  passdata:any;
  responsePass:any;
  imageId:any;
  imagesrc:any = "../../assets/shared/images/noprofilepic.jpg";
  submitted=false;
  submitted1=false;
  phonecheck:any;
  passwordForm:FormGroup;
  profileForm:FormGroup;
 confirmPasswordError:any;
 fieldTextType: boolean;
 fieldTextType1: boolean;
 fieldTextType2: boolean;

  ngOnInit(): void {

    

    this.profileForm=new FormGroup({
      FirstName: new FormControl(null),
      UserName: new FormControl(null),
      Email: new FormControl(null),
      About: new FormControl(null,Validators.required),
      Twitter:new FormControl(null,Validators.required),
      Facebook : new FormControl(null,Validators.required),
      Instagram: new FormControl(null,Validators.required),
      PhoneNumber: new FormControl(null,Validators.required),
      ProfilePicture : new FormControl(null,Validators.required)
    });
    
    this.passwordForm=new FormGroup({
      OldPassword: new FormControl(null,Validators.required),
      NewPassword:new FormControl(null,[Validators.required, Validators.minLength(6)]),
      ConfirmPassword:new FormControl(null,[Validators.required, Validators.minLength(6)])
    });

    
    this.refreshProfile();
    
  }

  onSubmit(): void{
    this.submitted = true;

        // stop here if form is invalid
        if (this.profileForm.invalid) {
            return;
        }
    // console.log(this.profileForm);
    this.updateUserv();
    this.refreshProfile();
    
    window.location.reload();
    // alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.profileForm.value, null, 4));
    
  }


  get f() { return this.profileForm.controls; }


  get f1() {return this.passwordForm.controls;}



  onSubmit1(): void {

    this.submitted1 = true;

        // stop here if form is invalid
        if (this.passwordForm.invalid) {
            return;
        }
        
    // console.log(this.passwordForm.value);
    this. updatePassword();
    this.refreshProfile();
   
    window.location.reload();
    
    //alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.passwordForm.value, null, 4));
  }
  refreshProfile(){
    this.service.getUserInfo(this.currentUser).subscribe(
      data=>{this.pro=data;
        if(this.pro.ProfilePicture)
        {this.getUserImage();
        };
        this.profileForm=new FormGroup({
          FirstName: new FormControl(data.FirstName),
          UserName: new FormControl(data.UserName),
          Email: new FormControl(data.Email),
          About: new FormControl(data.About,Validators.required),
          Twitter:new FormControl(data.Twitter,Validators.required),
          Facebook : new FormControl(data.Facebook,Validators.required),
          Instagram: new FormControl(data.Instagram,Validators.required),
          PhoneNumber: new FormControl(data.PhoneNumber,Validators.required),
          ProfilePicture : new FormControl(data.ProfilePicture,Validators.required)
        });
        this.titleService.setTitle(this.pro.UserName);
        this.metaTagService.addTags([
          {name:"description", content:this.pro.About},
          {name:"author", content: this.pro.UserName},
          {name:"robots", content:"index,follow"},
          {name:"keywords",content:"profile,"+this.pro.UserName+","+this.pro.FirstName},
          {httpEquiv:'Content-Type', content:"text/html", charset:"UTF-8"}                       
          ]);
      }
    );}
    updateUserv(){
      this.service.updateUser(this.profileForm.value).subscribe(
        data=>{this.responseUser=data;}
      )

    }
    updatePassword(){
      this.service.updatePassword(this.passwordForm.value).subscribe(
        data=>{this.responsePass=data;}
      )
    }
    getUserImage(){
    this.service.getImage(this.pro.ProfilePicture).subscribe((baseImage : any) => {
      let objectURL = URL.createObjectURL(baseImage);
       this.imagesrc = this.sanitizer.bypassSecurityTrustUrl(objectURL);
    });}

    goBack()
    {
      this.router.navigate(['/publicprofile/'+this.currentUser]);
    }

    browseFile()
    {
      document.getElementById('fileButton').click();
      // alert("you need to give Save changes in order to change photo")
    }
    onFileSelected(e: Event): void {
      this.maxLimitError =false;
      const target = e.target as HTMLInputElement;
      if (target.files && target.files[0]) {
        let size = ((target.files[0].size/1024)/1024);
        if(size > 3)
        {
          this.maxLimitError = true;
          return;
        }
        const imageFile = target.files[0];
        const fileReader = new FileReader();
        fileReader.onload = () => {
          return this.imagesrc = fileReader.result;
        };    
        fileReader.readAsDataURL(imageFile);
        var form = new FormData();
        form.append('upload_file',imageFile);
        form.append('mediaType','dp');
        this.service.updateImage(form).subscribe((data)=>{
          this.profileForm.controls.ProfilePicture.setValue(data.Id);
          // console.log(this.profileForm.value.ProfilePicture);
        })
      }
    }
    checkPassword()
  {
    this.confirmPasswordError = false;
    if(this.passwordForm.value.NewPassword !== this.passwordForm.value.ConfirmPassword)
    {
      this.confirmPasswordError = true;
    }
  }
  toggleFieldTextType() {
    this.fieldTextType = !this.fieldTextType;
  }
  toggleFieldTextType1() {
    this.fieldTextType1 = !this.fieldTextType1;
  }
  toggleFieldTextType2() {
    this.fieldTextType2 = !this.fieldTextType2;
  }
}