import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { concatMap, filter, map } from 'rxjs/operators';
import { BehaviorSubject, Observable } from 'rxjs';
import { DomSanitizer } from '@angular/platform-browser';
import { identifierModuleUrl } from '@angular/compiler';

@Injectable({
  providedIn: 'root'
})
export class BlogService {
  api = environment.SERVER_URL;
  latestBlogs : any[];
  popularBlogs : any[];
  technologyBlogs : any[];
 
  constructor(private http: HttpClient, private sanitizer : DomSanitizer) { 

  }

  getFeaturingBlogs(blogType,perPage){
    return this.http.get(`${this.api}/api/Blog/feature?blogType=${blogType}&perPage=${perPage}`)
    .pipe(map(data=>{
      let tempArr : any[] = [];
      let blogI ={};
      let tempData =Object.values(data); 
      tempData.forEach(async blog=>{       
        blogI = {          
          Id : blog.Id,
          Title : blog.Title,
          Subtitle : blog.Subtitle,
          Author : blog.Author,
          MediaId : blog.MediaId,
          Category : blog.Category,
          Likes : blog.Likes,
          Media : null,
          CreatedTime : blog.CreatedTime,
          LastUpdatedTime : blog.LastUpdatedTime
        }        
        tempArr.push(blogI);
      })
      return tempArr;
    }));
  }

  getCategoryXBlogs(categoryId){
    return this.http.get(`${this.api}/api/Blog/category?category=${categoryId}`)
    .pipe(map(data=>{
      let tempArr : any[] = [];
      let technologyI ={};
      let tempData =Object.values(data); 
      tempData.forEach(async blog=>{       
        technologyI = {          
          Id : blog.Id,
          Title : blog.Title,
          Subtitle : blog.Subtitle,
          Author : blog.Author,
          MediaId : blog.MediaId,
          Category : blog.Category,
          Likes : blog.Likes,
          Media : null,
          CreatedTime : blog.CreatedTime,
          LastUpdatedTime : blog.LastUpdatedTime
        }        
        tempArr.push(technologyI);
      })
      return tempArr;
    }));
  }


  async ImageAdder(blog)
  {
      if(blog.MediaId != null)
      {
        let baseImage = await this.getImage(blog.MediaId);
        let objectURL = URL.createObjectURL(baseImage);
        blog['Media'] = this.sanitizer.bypassSecurityTrustUrl(objectURL);     
      }
  }

  getImage(imageUrl: string): Promise<Blob> {
    return this.http.get(this.api+'/api/media/'+imageUrl, { responseType: 'blob' }).toPromise();
  }

 //*****************************************Single Blog Service ***********************************//
  
  getSingleBlog(Id:any):Observable<any>
  {
    return this.http.get<any>(this.api+'/api/Blog/'+Id);
  }
  
  UpdateComments( Comment :any){
     return this.http.post(this.api+'/api/comments',Comment);
  }


 /************************************************Get All Categories***************************** */
 getAllCategories():Observable<any>{
   return this.http.get<any>(this.api+'/api/Categories');
 }
 /****************************Update Image****************************************************** */
 updateImage(form: any): Observable<any> {  
  return this.http.post(this.api+'/api/media',form);
}
/*********************************Posting a Blog************************************************* */
 createBlog(form:any, currentUser,mediaId) : Observable<any>{
   return this.http.post(this.api+'/api/Blog',{
    "Title" : form.Title,
    "Subtitle" : form.Subtitle,
    "Content" : form.Editor,
    "CategoryId" : form.Category,
    "Author" : currentUser,
    "Likes" : 0,
    "CreatedTime" : new Date(),
    "LastUpdatedTime" : new Date(),
    "MediaId":mediaId
   });
 }

 updateBlog(form:any,currentUser,blogId,Likes,mediaId):Observable<any>{
  return this.http.put(this.api+'/api/Blog/'+blogId,{
   "Title" : form.Title,
   "Subtitle" : form.Subtitle,
   "Content" : form.Editor,
   "CategoryId" : form.Category,
   "Author" : currentUser,
   "Likes" : Likes,
   "LastUpdatedTime" : new Date(),
   "MediaId":mediaId,
   "CreatedTime" : new Date()

  });
}

/*********************************** Search Blogs using Category and Input *********************** */
 searchBlogs (categoryId = 0, searchValue) : Observable<any>
 {
   return this.http.get(`${this.api}/api/Blog/searchBlogs?categoryId=${categoryId}&searchValue=${searchValue}`)
   .pipe(map(data=>{
    let tempArr : any[] = [];
    let technologyI ={};
    let tempData =Object.values(data); 
    tempData.forEach(async blog=>{       
      technologyI = {          
        Id : blog.Id,
        Title : blog.Title,
        Subtitle : blog.Subtitle,
        Content : blog.Content,
        CategoryId : blog.CategoryId,
        MediaId : blog.MediaId,
        Media : null,
        Author : blog.Author,
        CategoryName : blog.CategoryNavigator.CategoryName,
        Likes : blog.Likes,
        CreatedTime : blog.CreatedTime,
        LastUpdatedTime : blog.LastUpdatedTime
      }        
      tempArr.push(technologyI);
    })
    return tempArr;
  }));
 }

 /************************** Add Likes **********************************/
 updateLike(blogId) : Observable<any>
 {
   return this.http.get(`${this.api}/api/Blog/addlike?blogId=${blogId}`);
 }

 /************************* Add Bookmark *******************************/
 updateBookmark(blogId,userName) : Observable<any>
 {
   return this.http.post(`${this.api}/api/bookmark`,{
     "BlogId" : blogId,
     "UserId" : userName
    });
 }
/********************************Delete Comment**************************/
deleteComment(id){
  return this.http.delete(this.api+"/api/comments/"+id);
}

addViews(id)
{
  return this.http.get(`${this.api}/api/blog/addviews?blogId=${id}`);
}


}




