import { HttpClient, HttpErrorResponse, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import {environment} from 'src/environments/environment';
import Swal from 'sweetalert2';
@Injectable({
  providedIn: 'root'
})

export class AuthService {

  api = environment.SERVER_URL;
  tokenExpirationTimer : any;
  isLoggedIn = new BehaviorSubject<boolean>(null);
  userAuthenticated: any;

  constructor(private http: HttpClient, private router:Router) {
    if (localStorage.getItem('User')) {
      this.isLoggedIn.next(true);
      this.userAuthenticated = true;
    }
  }

  isAuthenticated(): any {
    const user = JSON.parse(localStorage.getItem('User'));
    return user ? user : 'No user';
  }

  autoLogin()
  {
      const userData : {
          access_token : string;
          token_type : string;
          expires_in : string;
          userName : string;
          issued : string;
          _expires : string;
      } = JSON.parse(localStorage.getItem('User'));
      if(!userData)
        return;
      console.log("autologin userdata..");
      if(userData.access_token)
      {
          const expirationDuration = new Date(parseInt(userData.expires_in) *1000).getTime();
          this.autoLogout(expirationDuration);
      }
  }

  autoLogout(expirationDuration : number)
  {
      console.log("autologout started!");
      this.tokenExpirationTimer = setTimeout(()=>{
          Swal.fire({
              title: 'Info!',
              text: "Your session has been timed out",
              icon: 'info',
              confirmButtonText: 'Close'
            })
          this.logOut();
      },expirationDuration);
  }

  logOut()
  {
      this.router.navigate(['/login']);
      localStorage.removeItem('User');
      if(this.tokenExpirationTimer)
      {
          clearTimeout(this.tokenExpirationTimer);
      }
      this.tokenExpirationTimer = null;
  }

  private handleError(errorResponse : HttpErrorResponse)
  {
      let errorMessage = 'An unknown error occurred';
      if(!errorResponse.error || !errorResponse.error.message)
      {
          return throwError(errorMessage);
      }
      else
      {
        return throwError(errorResponse.error.message);
      }
  }
  
  private handleAuthentication(username:string, token:string, expiresIn:number)
  {
      const expirationDate = new Date(new Date().getTime() + expiresIn * 1000);
      const user = new User(
          username,
          token,
          expirationDate);
      console.log("handleAuth : ")
      this.autoLogout(expiresIn * 1000);
      localStorage.setItem('User',JSON.stringify(user));
  }

  getAllUsers()
  {
    return this.http.get(`${this.api}/api/account/allusers`);
  }

  loginWithGoogle(accessToken : any) : Observable<any> {
    return this.http.get(`${this.api}/api/account/loginwithGoogle?accessToken=${accessToken}`)
    .pipe(catchError(this.handleError),
        tap(responseData =>{
            this.handleAuthentication(
                responseData.userName,
                responseData.access_token,
                responseData.expires_in)
                }
            )
        );
  }

  login(credentials: any): Observable<any> {
    const body = new HttpParams()
    .set('username', credentials.username)
    .set('password', credentials.password)
    .set('grant_type','password');
    return this.http.post(`${this.api}/Token`,
    body.toString(),
    {
      headers: new HttpHeaders()
        .set('Content-Type', 'application/x-www-form-urlencoded')     
    }
    )
    .pipe(catchError(this.handleError),
        tap(responseData =>{
            this.handleAuthentication(
                responseData.userName,
                responseData.access_token,
                responseData.expires_in)
                }
            )
        );
  }

  register(userData:any) : Observable<any> {
    return this.http.post(`${this.api}/api/account/register`,{
      "UserName" : userData.username,
      "FirstName" : userData.firstname,
      "LastName" : userData.lastname,
      "Password" : userData.password,
      "ConfirmPassword" : userData.password,
      "Email" : userData.email,
      "DOB" : userData.dob
    });
  }
}

export class User {
  constructor(
      public username:string, 
      public access_token:string, 
      public expires_in:Date
      ){}

      get token()
      {
          if(!this.expires_in || new Date() > this.expires_in)
              return null;
          return this.access_token;
      }
}
