import { Component, OnInit } from '@angular/core';
import { ContactusService } from '../services/contactus.service';
import { Title, Meta } from '@angular/platform-browser';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AlertDialogComponent } from '../utility/alert-dialog/alert-dialog.component';
@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit {

  employee:string;
  Name:string;
  email:string;
  message:string;
  dialog:string;

  subscribe:string;

  constructor(public contactusservice:ContactusService, private titleService: Title,private metaTagService: Meta,private dialogComponent: MatDialog ) { }

  ngOnInit(): void {

    this.titleService.setTitle("About Us");
    this.metaTagService.addTags([
      {name:"description", content:"Learn about our journey and contact us"},
      {name:"author", content: "Piyush Chouhan M, Nishant Bhushan, Kedar R, Mohanavel R, Kumaran R"},
      {name:"robots", content:"index,follow"},
      {name:"keywords",content:"about us, contact"},
      {httpEquiv:'Content-Type', content:"text/html", charset:"UTF-8"}                       
      ]);

  }

    
  createSubscribe()
  {
    let Record1 = {};
    Record1['subscribe'] = this.subscribe;
    
    this.contactusservice.create_subscribe(Record1).then(res=>{
      this.subscribe="";
      this.dialogComponent.open(AlertDialogComponent, { data: { title: 'Success', content: 'You have successfully subscribed to our news letter.' } });
    }).catch(error=>{
      // console.log("Subscribe error",error);
    });
  }
}
