import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { __param } from 'tslib';
import { BlogService } from '../services/blog.service';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { Title, Meta } from '@angular/platform-browser';
@Component({
  selector: 'app-blogs',
  templateUrl: './blogs.component.html',
  styleUrls: ['./blogs.component.css']
})
export class BlogsComponent implements OnInit {
  categories : any[] =[];
  popularBlogs : any[] =[];
  blogsData : any[] =[];
  selection : any = 1;
  itemsPerPage : any = 10;
  searchValue : any = null;
  constructor(private router:Router,private blogService:BlogService,private titleService: Title,private metaTagService: Meta) {

    this.blogService.getCategoryXBlogs(1).subscribe((data:any[])=>{
      data.forEach(blog =>{
        this.blogService.ImageAdder(blog).then();
      })
      this.blogsData = data;
      
    })
    
    this.blogService.getFeaturingBlogs("popular",12).subscribe((data:any[])=>{
      data.forEach(blog =>{
        this.blogService.ImageAdder(blog).then();
      })
      this.popularBlogs = data;
    })
   }

  ngOnInit(): void {

    //Meta tags for SEO
    this.titleService.setTitle("Explore blogs");
    this.metaTagService.updateTag({name:"description", content:"Search for blogs and browse it."});
    this.metaTagService.updateTag({name:"author", content: "Piyush Chouhan M, Nishant Bhushan, Kedar R, Mohanavel R, Kumaran R"});
    this.metaTagService.updateTag({name:"robots", content:"index,follow"});
    this.metaTagService.updateTag({name:"keywords",content:"Browse, search blogs, filter blogs, explore, categorical search, find by title"});
    this.metaTagService.updateTag({httpEquiv:'Content-Type', content:"text/html", charset:"UTF-8"});
      
    this.blogService.getAllCategories().subscribe((data)=>{
      this.categories = data;
    })

    this.blogService.getAllCategories().subscribe((data:any[])=>{
      this.categories = data;
    })
  }

  startSearch(search,category)
  {
    // console.log(search,category);
    if(search=='')
      return;
    this.searchValue = search;
    this.itemsPerPage = 10;
    this.blogService.searchBlogs(category,search).subscribe((data)=>{
      // console.log(data);
      data.forEach(blog =>{
        this.blogService.ImageAdder(blog).then();
      })
      this.blogsData = data;
      console.log(this.blogsData);
    },()=>{
      this.blogsData = [];
    })
  }

  changeSelection(category)
  {
    this.selection = category;
    this.searchValue = null;
    this.blogService.getCategoryXBlogs(this.selection).subscribe((data:any[])=>{
      data.forEach(blog =>{
        this.blogService.ImageAdder(blog).then();
      })
      this.blogsData = data;
      // console.log(this.blogsData);
    })
    // console.log(this.selection);
  }


  customOptions: OwlOptions = {
    loop: false,
    rewind:true,
    mouseDrag: false,
    touchDrag: true,
    pullDrag: false,
    dots: false,
    navSpeed: 700,
    navText: ["<i class='fa fa-angle-left'></i>", "<i class='fa fa-angle-right'></i>"],
    items:1,
    nav: true
  }
}
