import { Component, OnInit, SimpleChanges, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import {FaqServiceService} from '../services/faq-service.service';
import { AdminpageService } from '../services/adminpage.service';
import { FormControl, FormGroup } from '@angular/forms';
import { AuthService } from '../services/auth.service';
import { ReportService } from '../services/report.service';
import Swal from 'sweetalert2';
import { ConfirmationComponent } from '../utility/confirmation/confirmation.component';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-adminpage',
  templateUrl: './adminpage.component.html',
  styleUrls: ['./adminpage.component.css']
})

export class AdminpageComponent implements OnInit {

  blogColumns = ['id', 'title', 'author', 'category','time', 'actions'];
  commentColumns = ['id','comment','author','time','actions'];
  faqColumns = ['id','question','solution','vote','actions'];
  reportColumns = ["id",'contenttype','contentid','reporttype','reportedcount','actions'];
  
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild('blogpaginator') paginator: MatPaginator;
  @ViewChild('commentpaginator') commentpaginator: MatPaginator;
  @ViewChild('FAQpaginator') faqpaginator: MatPaginator;
  @ViewChild('Reportpaginator') reportpaginator: MatPaginator;

  searchKey: string;
  reviewComment: any;
  
  faqList: any=[];
  allBlogs: any=[];
  allComments:any=[];
  allReports : any = [];
  dataSource = new MatTableDataSource(null);
  commentSource = new MatTableDataSource(null);
  faqSource = new MatTableDataSource(null);
  reportSource = new MatTableDataSource(null);
  faqid:any;
  currentUser : boolean =false;
  addForm: FormGroup;
  errorLogs : any;
  
  
  constructor(private service: FaqServiceService,
              private adminservice:AdminpageService,
              private authService : AuthService, 
              private report:ReportService,
              private dialog: MatDialog ) { 
    this.refreshfaqList();
    this.getallBlogs();
    this.getallcomments();
    this.getReports();
    this.getErrorLogs();
    let user = this.authService.isAuthenticated().userName;
    if(user == "Legion@R00t")
      this.currentUser = true;
    
  }

  ngAfterViewInit() {
    // this.dataSource.paginator = this.paginator;
  }

  

  ngOnInit(): void {
    // console.log("Oninit",this.dataSource);
    this.addForm = new FormGroup({
      question:new FormControl(null),
      solution:new FormControl(null)
    });
  }

  refreshfaqList(){
    this.service.getFAQList().subscribe(
      data=>{this.faqList=data;
        this.faqSource = new MatTableDataSource(this.faqList);
        this.faqSource.paginator = this.faqpaginator;
    });
  }

  onSearchClear()
  {
    this.searchKey="";
    this.applyFilter();
  }

  applyFilter()
  {
    this.dataSource.filter = this.searchKey.trim().toLowerCase();
    this.commentSource.filter = this.searchKey.trim().toLowerCase();
    this.faqSource.filter = this.searchKey.trim().toLowerCase();
    this.reportSource.filter = this.searchKey.trim().toLowerCase();
  }


  getallBlogs()
  {
    this.adminservice.getAllBlogs().subscribe(data=>{
      this.allBlogs = data;
      this.dataSource = new MatTableDataSource(this.allBlogs);
      this.dataSource.paginator = this.paginator;
    });
  }

  getallcomments()
  {
    this.adminservice.getAllComments().subscribe(data=>{
      this.allComments = data;
      this.commentSource = new MatTableDataSource(this.allComments);
      this.commentSource.paginator = this.commentpaginator;
    });
  }

  deleteblog(id)
  {
    this.adminservice.deleteblog(id).subscribe(async (response)=>{
      const {value:accept} = await Swal.fire({
        icon: 'success',
        title: 'Blog deleted successfully',
        showConfirmButton: false,
        timer: 1500
      });
      if(accept == null)
        window.location.reload();
    });
  }

  deletefaq(id)
  {
    this.adminservice.deletefaq(id).subscribe(async (response)=>{
    const {value:accept} = await Swal.fire({
        icon: 'success',
        title: 'FAQ deleted successfully',
        showConfirmButton: false,
        timer: 1500
      })
      if(accept == null)
        window.location.reload();
    })
  }

  deleteComment(id)
  {
    this.adminservice.deletecomment(id).subscribe(async (response)=>{
      const {value:accept} = await Swal.fire({
          icon: 'success',
          title: 'Comment deleted successfully',
          showConfirmButton: false,
          timer: 1500
        })
      if(accept == null)
        window.location.reload();
    });
  }

  onSubmitAdd()
  {
    // console.log("Form",this.addForm.value);
    if(this.faqid)
    {
      this.updateFaq();
    }
    else
    {
      this.addFaq();
    }
    //window.location.reload();

  }

  addFaq()
  {
    this.adminservice.addFAQ(this.addForm.value).subscribe((response)=>{
      Swal.fire({
        icon: 'success',
        title: 'FAQ added successfully',
        showConfirmButton: false,
        timer: 1500
      })
    });
  }

  updateFaq()
  {
    this.adminservice.updateFAQ(this.faqid,this.addForm.value).subscribe(response=>{
      Swal.fire({
        icon: 'success',
        title: 'FAQ updated successfully',
        showConfirmButton: false,
        timer: 1500
      })
    });
  }
  abc:any;
  editFaq(id)
  {
    this.faqid = id;
    // console.log(this.faqid);
    document.getElementById('editButton').click();
    this.adminservice.getFAQid(this.faqid).subscribe(data=>{
      this.abc = data;
      // console.log("Edit data",this.abc);
      this.addForm = new FormGroup({
        question:new FormControl(data.Question),
        solution:new FormControl(data.Solution)
      });
    });
  }

  WindowReload()
  {
    window.location.reload();
  }


  getReports()
  {
    this.report.getReports().subscribe((data)=>{
      this.allReports = data;
      this.reportSource = new MatTableDataSource(this.allReports);
      this.reportSource.paginator = this.reportpaginator;
    })
  }

  viewContent(contentType, contentId)
  {
    if(contentType === "Blog")
    {
      window.location.href = "/blog/"+contentId;
    }
    else
    {
      this.adminservice.getComment(contentId).subscribe((data)=>{
        this.reviewComment = data;
        document.getElementById('viewCommentModal').click();
      })
    }
  }

  deleteContent(reportId,contentType, contentId)
  {
    if(contentType === "Blog")
    {
      const dialogRef = this.dialog.open(ConfirmationComponent, {
        data: { process: "delete" }
      });
      dialogRef.afterClosed().subscribe(result => {
        if (result == true) {
          this.report.deleteReport(reportId).subscribe((response)=>{
            this.deleteblog(contentId);
            window.location.reload();
          });
        }
      });
    }
    else
    {
      const dialogRef = this.dialog.open(ConfirmationComponent, {
        data: { process: "delete" }
      });
      dialogRef.afterClosed().subscribe(result => {
        if (result == true) {
          this.report.deleteReport(reportId).subscribe((response)=>{
            this.deleteComment(contentId);
            window.location.reload();
          });
        }
      });
    }
  }

  deleteReport(Id)
  {
    this.report.deleteReport(Id).subscribe(async (response)=>{
      const {value : accept} = await Swal.fire({
        icon: 'success',
        title: 'No objection applied to the report',
        confirmButtonText : 'Done'
      })
      if(accept)
      {
        window.location.reload();
      }
    });
  }

  getErrorLogs()
  {
    this.adminservice.getErrorLogs().subscribe((file:any)=>{
      this.errorLogs = file;
      // console.log(file);
    })
  }


}
