import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutComponent } from './about/about.component';
import { AdminpageComponent } from './adminpage/adminpage.component';
import { BlogComponent } from './blog/blog.component';
import { BlogsComponent } from './blogs/blogs.component';
import { CreateBlogComponent } from './create-blog/create-blog.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { FaqComponent } from './faq/faq.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { ProfileComponent } from './profile/profile.component';
import { PublicProfileComponent } from './public-profile/public-profile.component';
import { RegisterComponent } from './register/register.component';
import {AuthGuardService} from './services/auth-guard.service';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  {path:"login",component:LoginComponent},
  {path:"blogs",component:BlogsComponent},
  {path:"register", component:RegisterComponent},
  {path:"home",component:HomeComponent},
  {path:"about", component:AboutComponent},
  {path:"faq",component:FaqComponent},
  {path:"publicprofile/:userName",component:PublicProfileComponent},
  {path:"editprofile",component:ProfileComponent, canActivate:[AuthGuardService]},
  {path:"dashboard",component:DashboardComponent, canActivate:[AuthGuardService]},
  {path:"blog/:Id",component:BlogComponent},
  {path:"create",component:CreateBlogComponent, canActivate:[AuthGuardService]},
  {path:"edit/:Id",component:CreateBlogComponent, canActivate:[AuthGuardService]},
  {path:"adminpage",component:AdminpageComponent, canActivate:[AuthGuardService]},

  // {path:"404", component: PageNotFoundComponent},
  { path:"**", component: PageNotFoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
